.estrellas{
    padding-left: 2px;
    padding-right: 2px;
    height: 1em;
    width: 1em;
}

.flexFatherRowOCL{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items:center;
    box-sizing: border-box;
}

.esp65porOCL{
    width: 65%;
}

.esp35porOCL{
    width: 35%;
}


@media only screen and (max-width: 1000px) {
    .flexFatherRowOCL{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items:center;
        box-sizing: border-box;
    }
    
    .esp65porOCL{
        display: flex;
        align-content: center;
        justify-content: center;
        width: 100%;
    }
    
    .esp35porOCL{
        width: 100%;
    }

    .textoJustificado{
        text-align: center;
        width: 100%;
    }
}

